<template>
  <div id="loading-container" class="vs-con-loading__container">
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <form-wizard ref="wizard"
                   color="rgba(var(--vs-primary), 1)"
                   :title="null" :subtitle="null"
                   :nextButtonText="$t('NEXT')"
                   :backButtonText="$t('PREVIOUS')">

        <template slot="custom-buttons-left" slot-scope="props">
          <vs-button class="mr-2" color="primary" type="border" @click="cancel" v-if="props.activeTabIndex === 0">{{ $t('CANCEL') }}</vs-button>
        </template>
        <template slot="finish">
          <div class="dropdown-button-container">
            <vs-button class="btnx" type="filled" @click="modifyBeneficiaryType('browse')">{{ $t('SUBMIT') }}</vs-button>
            <vs-dropdown vs-trigger-click>
              <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
              <vs-dropdown-menu>
                <vs-dropdown-item @click="modifyBeneficiaryType('view')">
                  <span>{{ $t('SUBMIT_VIEW') }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </template>

        <tab-content :title="$t('GENERAL')" icon="far fa-tag">
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('TYPE_NAME') }}</label>
              <vs-input class="w-full" v-model="beneficiaryType.typeName"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('PARENT_TYPE') }}</label>
              <beneficiary-type-lookup :title="$t('PARENT_TYPE')" :recordValue="beneficiaryType.parentTypeId" :recordDisplay="beneficiaryType.parentTypeName" valueField="typeId" displayField="typeName" @select-record="selectBeneficiaryTypeRecord"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('TYPE_BEHAVIOUR') }}</label>
              <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="beneficiaryType.typeBehaviourEnumId">
                <vs-select-item v-for="(enumeration, index) in typeBehaviours" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
              </vs-select>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('EXTERNAL_ID') }}</label>
              <vs-input class="w-full" v-model="beneficiaryType.externalTypeId"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <label class="vs-input--label">{{ $t('POSITION') }}</label>
              <div class="flex">
                <vs-input-number v-model="beneficiaryType.position" min="0"/>
              </div>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <vs-checkbox v-model="beneficiaryType.disabled">{{ $t('DISABLED') }}</vs-checkbox>
            </div>
          </div>
        </tab-content>

        <tab-content :title="$t('NOTES')" icon="far fa-sticky-note">
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <label class="vs-input--label">{{ $t('INPUT_NOTES') }}</label>
              <div class="code-editor">
                <codemirror v-model="beneficiaryType.inputNotes" :options="vueOptions"></codemirror>
              </div>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <label class="vs-input--label">{{ $t('REVIEW_NOTES') }}</label>
              <div class="code-editor">
                <codemirror ref="editor2" v-model="beneficiaryType.reviewNotes" :options="vueOptions"></codemirror>
              </div>
            </div>
          </div>
        </tab-content>
      </form-wizard>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import CodeEditorMixin from '@/mixins/commons/CodeEditorMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import BeneficiaryTypeLookup from '@/views/beneficiary/beneficiary-type/BeneficiaryTypeLookup'
import BeneficiaryTypeService from '@/services/beneficiary/BeneficiaryTypeService'
import EnumerationService from '@/services/commons/EnumerationService'
import { FormWizard, TabContent } from 'vue-form-wizard'

export default {
  name: 'beneficiary-type-modify',
  mixins: [AlertMixin, CodeEditorMixin, LoadingMixin],
  components: {
    BeneficiaryTypeLookup,
    FormWizard,
    TabContent
  },
  data: function () {
    return {
      beneficiaryType: {},
      typeBehaviours: []
    }
  },
  methods: {
    async load () {
      let self = this
      BeneficiaryTypeService.beneficiaryTypes({
        typeId: this.$route.params.id
      }).then(function (response) {
        self.beneficiaryType = response.data.beneficiaryTypeList[0] || {}
        self.beneficiaryType.disabled = self.beneficiaryType.disabled === 'Y'
      })

      EnumerationService.enumerations({
        enumTypeId: 'BeneficiaryTypeBehaviour',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.typeBehaviours = response.data.enumerationList || []
      })
    },
    selectBeneficiaryTypeRecord (record) {
      this.beneficiaryType.parentTypeId = record.typeId || ''
    },
    modifyBeneficiaryType (nextAction) {
      let self = this
      this.startLoading('loading-container')
      BeneficiaryTypeService.modifyBeneficiaryType({
        typeId: this.$route.params.id,
        typeName: this.beneficiaryType.typeName,
        parentTypeId: this.beneficiaryType.parentTypeId,
        typeBehaviourEnumId: this.beneficiaryType.typeBehaviourEnumId,
        externalTypeId: this.beneficiaryType.externalTypeId,
        disabled: this.beneficiaryType.disabled ? 'Y' : 'N',
        position: this.beneficiaryType.position,
        inputNotes: this.beneficiaryType.inputNotes,
        reviewNotes: this.beneficiaryType.reviewNotes
      }).then(function (response) {
        self.stopLoading('loading-container')
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewBeneficiaryType', params: { id: response.data.typeId } })
          } else {
            self.$router.push({ name: 'BrowseBeneficiaryTypes' })
          }
        }
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted: function () {
    this.load()
  }
}
</script>
